import React from 'react'
import NavbarStyle from './Navbar.module.css'
import {Link} from 'react-router-dom'

const Navbar = props=>{

    const ToggleNavbar = ()=>{
        const e = document.getElementsByName("vertical")[0];
        if(e !== null){
            if(e.classList.value.match("Vertical"))
            {
                e.classList.value=""
                e.classList.add(NavbarStyle.Disabled)
            }
            else
            {
                e.classList.value=""
                e.classList.add(NavbarStyle.Vertical)
            }
        }
    }

    return(
        <div className={NavbarStyle.Block}>
            <span className={NavbarStyle.Header}>Haolin Lyu (Tim)</span>
            <ul className={NavbarStyle.Navbar}>
                <li className={NavbarStyle.Tab}><Link to="/projects">Projects</Link></li>
                <li className={NavbarStyle.Tab}><Link to="/">Personal Profile</Link></li>
            </ul>
            <div onClick={ToggleNavbar}  className={NavbarStyle.Toggle}>
                <span  >               
                </span>
                <ul name="vertical" className={NavbarStyle.Disabled}>
                    <li><Link to="/projects">Projects</Link></li>
                    <li><Link to="/">Personal Profile</Link></li>
                </ul>
            </div>
        </div>
    )
}

export default Navbar;