import React from 'react'
import ProjectStyle from './Project.module.css'
import ProjStyle from './Project.module.css'

class Project extends React.Component{
    

    openGit=(url)=>{
        window.open(url,"","width=800px height=640px top=250px left=250px")
    }
    
    render(){
        const Image = (this.props.src.length === 1)?<img src={this.props.src[0]} alt="whoops, where is my project snapshot :("></img>:
        <div className={ProjectStyle.SlideImage}>
           {Object.assign([],this.props.src).map(s=>{return <img src={s} alt="whoops, where is my project snapshot :("></img>})} 
        </div>
        return(
            <div  onClick={()=>this.openGit(this.props.link)}  className={ProjectStyle.Block}>
                <p className={ProjectStyle.Intro}>{this.props.intro}</p>
                     {Image}         
                <div className={ProjStyle.Float}>
                </div>
            </div>
        )
    }
}

export default Project